    .post-item {
    position: relative;
    background-color: #f3efea;
    border-radius: 10px;
    max-width: 250px;
    max-height: 250px;
    padding: 5px;
    margin-bottom: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    }
    .post-item p {
    max-height: 250px;
    overflow-y: scroll;
    scrollbar-width: none;
    margin: 0;
    padding-right: 10px;
    } 
    
    .post-item:before {
    content: '';
    position: absolute;
    left: 5%;
    width: 14px;
    height: 14px;
    background-color: rgb(219, 175, 73);
    border-radius: 50%;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    transform: translateX(-50%);
    }
    
    .post-header {
    display: flex;
    justify-content: flex-end;
    }
    
    .post-header svg {
    cursor: pointer;
    color: #333;
    }
